<template>
  <div class="quiz">
      <div class="form">
        <div class="title">
          <h2>{{dados.description}}</h2>
          <span>{{dados.title}}</span>
        </div>
        <div class="row">
          <div class="col-6">
            <!-- <transition :duration="{ enter: 500, leave: 300 }" enter-active-class="animated zoomIn" leave-active-class="animated zoomOut" mode="out-in"> -->
            <div class="row" v-for="(quest, index) in dados.quiz" :key="index">
              <div v-if="quest.id === count & quest.type === 'text'" >
                <div class="question">
                  <span>{{quest.ask}}</span>
                </div>
                <div v-for="(per, index) in quest.options" :key="index">
                  <div :id="index" class="questText">
                    <span  @click="somaPontos(index,  per.points)">{{per.option}}</span>
                  </div>
                </div>
              </div>
              <div v-if="quest.id === count & quest.type === 'image'" >
                <div class="question">
                  <span>{{quest.ask}}</span>
                </div>
                <div v-for="(per, index) in quest.options" :key="index+99">
                  <div class="col-12">
                    <img :id="index" class="imgQuest" @click="somaPontos(index, per.points)" :src="per.option">
                  </div>
                </div>
              </div>
            </div>
            <!-- </transition> -->
            <div class="submitQuiz" v-if="count > total & total != null">
              <h1>Parabéns! você finalizou nosso Quiz.</h1>
              <div class="submitText">
                <span>Para saber seu resultado, informe seu E-Mail abaixo e vamos enviar para você.</span>
                <input type="text" placeholder="exemplo@gmail.com" class="form-control">
                <button class="btn-submit">ENVIAR</button><br>
                <span>Teste result:{{points}}</span>
              </div>
            </div>
          </div>
          <div class="col-6">
            <div class="questImg">
              <img src="https://firebasestorage.googleapis.com/v0/b/project-smartquiz.appspot.com/o/quiz-images%2Fmieza%2Foitavobom%2Fmundi1.jpg?alt=media&token=ccf9441e-dd00-4cbd-8340-34c90af63fa9" alt="Quiz">
            </div>
          </div>
        </div>
      </div>
  </div>
</template>

<script>
const axios = require('axios')
export default {
  name: 'QuizComponent',
  data () {
    return {
      total: null,
      count: 1,
      points: null,
      dados: {
        description: null
      }
    }
  },
  beforeMount () {
    axios.get('https://project-smartquiz-default-rtdb.firebaseio.com/mieza/oitavo1.json')
      .then((response) => {
        this.dados = response.data
        this.total = this.dados.quiz.length
      })
  },
  methods: {
    somaPontos (id, points) {
      document.getElementById(id).classList.add('active')
      var ponto = points
      this.points = this.points + ponto
      setTimeout(() => {
        this.count++
      }, 1000)
    }
  }
}
</script>

<style lang="scss" scoped>
.quiz{
  position: fixed;
  z-index: 9;
  width: 100%;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  .form{
    width: 75%;
    background-color: #FFF;
    border-radius: 10px;
    padding: 40px 40px;
    box-shadow: 0px 8px 8px 0px #494949b7;
    .title{
      text-align: center;
      padding-bottom: 50px;
      h2{
        color: #3b4c9b;
        font-weight: bold;
        font-size: 35px;
        padding-bottom: 10px;
      }
      span{
        font-size: 20px;
        font-weight: 600;
        color: #3b4d9bc5;
      }
    }
    .question{
      font-size: 30px;
      line-height: 30px;
      font-weight: 600;
      color: rgb(46, 46, 46);
      margin-bottom: 30px;
    }
    .submitQuiz{
      h1{
        text-align: center;
        font-size: 35px;
        font-weight: 700;
        color: #3b4c9b;
      }
      .submitText{
        margin: 50px auto;
        width: 60%;
        text-align: center;
        font-weight: 600;
        .btn-submit{
          text-decoration: none;
          padding: 15px 100px;
          border-radius: 10px;
          border: none;
          font-weight: 700;
          color: #fff;
          background-color: #2347e9d8;
          &:hover{
            transition: 0.3s;
            background-color: #2347e9e7;
          }
        }
        input{
          margin-top: 20px;
          margin-bottom: 20px;
        }
      }
    }
    .active{
      color: rgb(1, 187, 48);
      border: 5px solid rgba(1, 187, 47, 0.699) !important;
      transition: 0.3s;
    }
    .activeText{
      color: rgb(1, 187, 48);
      border: 2px solid rgba(1, 187, 47, 0.699) !important;
      transition: 0.3s;
    }
    .questText{
      margin-bottom: 20px;
      width: 100%;
      padding: 10px 0;
      padding-left: 10px;
      border: 2px solid #222;
      border-radius: 10px;
      cursor: pointer;
      font-weight: 600;
    }
    .imgQuest{
      width: 150px;
      float: left;
      margin-right: 20%;
      margin-bottom: 5%;
      border: 5px solid #2222225e;
      cursor: pointer;
    }
    .questImg{
      img{
        width: 100%;
        border-radius: 10px;
      }
    }
  }
}
</style>
